*{
    margin: 0;
    padding: 0;
}

body{
    background-color: #dcebfa;
}

.profile{
    margin: 0 auto;
    margin-top: 30px;
    background-color: white;
    width: 270px;
    text-align: center;
    border-radius: 7px;
    box-shadow: 0 0 5px rgba(0,0,0,0.5);
}

.avatar{
    margin-top: 30px;
    border-radius: 50%;
}

.name {
    margin-top: 30px;
    margin-bottom: 0;
    font-size: 20px;
    font-weight: 600;
}

.tag{
    margin-top: 10px;
    margin-bottom: 0;
    font-size: 15px;
    opacity: 0.5;
}

.location{
    margin-top: 10px;
    opacity: 0.5;
    font-size: 15px;
}

.stats{
    margin-top: 30px;
    list-style: none;
    display: flex;
    justify-content: space-around;
}

.list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 90px;
    height: 90px;
    background-color: #edf4fa;
    border-top: 1px solid #dcebfa;
    border-left: 1px solid #dcebfa;
}

.list:first-child{
    border-left: none;
    border-radius: 0 0 0 7px;
}

.list:last-child{
    border-radius: 0 0 7px 0;
}

.label{
    font-size: 15px;
    margin-bottom: 5px;
}

.statistics{
    background-color: #fff;
    margin: 0 auto;
    width: 270px;
    text-align: center;
    margin-top: 50px;
    border-radius: 7px;
    box-shadow: 0 0 5px rgba(0,0,0,0.5);
}

.title{
    display: inline-block;
    height: 50px;
    margin-top: 25px;
}

.stat-list{
   display: flex;
   justify-content: space-around;
}

.item{
   list-style: none;
   display: flex;
   flex-direction: column;
   height: 50px;
   border-top: 1px solid #dcebfa;
   border-left: 1px solid #dcebfa;
   width: 53px;
}

.item:first-child{
    border-left: none;
}

.friends{
  text-align: center;
  width: 270px;
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 50px;
}

.friend-list{
    display: flex;
    flex-direction: column;
}

.item-fr{
  display: block;
  width: 270px;
  height: 70px;
  display: flex;
  background-color: #fff;
  margin-bottom: 10px;
  border-radius: 7px;
  box-shadow: 0 0 5px rgba(0,0,0,0.5);
}

.true{
  margin-top: 27px;
  margin-right: 7px;
  margin-left: 7px;
  height: 15px;
  width: 15px;
  background-color: green;
  border-radius: 50%;
}

.false{
    margin-top: 27px;
    margin-right: 7px;
    margin-left: 7px;
    height: 15px;
    width: 15px;
    background-color: red;
    border-radius: 50%;
}

.avatar-friends{
  border-radius: 7px;
  height: 55px;
  width: 55px;
  margin-top: 8px;
}

.name-fr{
  margin-top: 27px;
  margin-left: 10px;
}

.transaction-history{
    width: 800px;
    margin: 0 auto;
}

table{
    box-shadow: 0 0 5px rgba(0,0,0,0.5);
}

tr{
    text-align: center;
    height: 40px;
}

.string:nth-child(2n-1){
    background-color: #edf4fa;
}

thead{
    background-color: #246cf2;
}